    margin-top: 5px;
  }
}

.sidebar .footer__socials {
  display: flex;
  justify-content: flex-end;
}

.sidebar .socials__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  margin-left: 10px;
  border-radius: 50%;
  background-color: #777a8c;
}

.sidebar .socials__item .icon {
  fill: #1e202a;
  font-size: 16px;
}

.sidebar .footer__nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  margin-top: 20px;
}

@media only screen and (max-height: 640px) {
  .sidebar .footer__nav {
    margin-top: 10px;
  }
}

.sidebar .footer__nav-link {
  margin-left: 15px;
  margin-bottom: 5px;
  font-size: 12px;
}

.sidebar .sidebar__footer-copy {
  margin-top: 8px;
  font-size: 12px;
  font-weight: 300;
  text-align: right;
}

.modal-popup {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding-bottom: 20px;
  color: #777a8c;
  background-color: #1e202a;
  border-radius: 4px;
}

.modal-popup .modal__title {
  padding: 22px 25px 15px 0;
  margin: 0 20px 0 30px;
  font-size: 16px;
  font-weight: 300;
  text-align: left;
  border-bottom: 2px solid #282b38;
}

@media only screen and (max-width: 768px) {
