@import "reset";
@import "mixins";
@import "vars";
@import "generated/sprite-svg";
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  font-family: $font-primary;
  line-height: 1.6;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
  }
}

html,
body {
  height: 100%;
}

.wrapper {
  position: relative;
  display: flex;
  width: 100%;
  max-height: 100vh;
  font-family: $font-primary;
  color: #777a8c;
  background-color: #1b1c25;
  @media only screen and (max-width: 600px) {
    overflow-x: hidden;
  }
}

a,
.icon {
  color: inherit;
  transition: all 0.2s ease;
}

button {
  background-color: transparent;
  border: none;
  transition: all 0.2s ease;
}

.main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 0 30px 25px 30px;
  @media only screen and (max-width: 1200px) {
    padding: 0 1% 1% 1%;
  }
}

.scroll-y {
  overflow-y: scroll;
  &::-webkit-scrollbar {
    width: 5px;
  }
  &::-webkit-scrollbar-track {
    background-color: #16171f;
  }
  &::-webkit-scrollbar-thumb {
    background-color: #2b2e3d;
  }
}

// HEADER
.header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding-top: 15px;
  padding-bottom: 10px;
  border-bottom: 2px solid #1e202a;
}

.header__logo {
  max-width: 60px;
  img {
    width: 100%;
  }
}

.profile {
  position: relative;
  padding-bottom: 5px;
  @include flc;
  &:hover {
    cursor: pointer;
    .profile__menu {
      height: 60px;
      overflow: visible;
      top: 100%;
      opacity: 1;
    }
    .icon-chevron-down {
      transform: rotate(180deg);
    }
  }
}

.profile__open-btn {
  position: relative;
  margin-left: 10px;
}

.icon-chevron-down {
  fill: #777a8c;
  &:hover {
    fill: #fff;
  }
}

.profile__avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
}

.profile__menu {
  height: 0;
  opacity: 0.8;
  overflow: hidden;
  position: absolute;
  right: 0;
  top: 100%;
  width: 132px;
  display: flex;
  flex-direction: column;
  line-height: 1.3;
  z-index: 100;
  transition: all 0.4s ease;
}

.profile__menu-link {
  font-size: 14px;
  padding: 5px 10px;
  letter-spacing: 0.5px;
  border-bottom: 1px solid lighten(#242734, 10%);
  background-color: #242734;
  &:last-child {
    border-bottom: none;
  }
  &:hover {
    color: #eaeaea;
    background-color: lighten(#242734, 5%);
  }
}

// CONTENT
.topbar {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 20px;
  @media only screen and (max-width: 992px) {
    padding-bottom: 20px;
    justify-content: center;
  }
  @media only screen and (max-height: 680px) {
    padding-bottom: 30px;
  }
  @media only screen and (max-height: 560px) {
    padding-bottom: 40px;
  }
}

.topbar__total {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  @media only screen and (min-width: 992px) {
    flex-basis: 40%;
  }
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  @include rfs(24);
  .icon {
    font-size: 1em;
    fill: #bbbed0;
  }
}

.info__number {
  @include flc;
  font-size: 1em;
  font-weight: 300;
  color: #c3c6d9;
  .icon {
    margin-right: 5px;
  }
}

.info__caption {
  margin-top: 10px;
  text-transform: uppercase;
  font-size: 0.5em;
  font-weight: 900;
  color: #777a8c;
  white-space: nowrap;
}

.topbar__buttons {
  position: relative;
  display: flex;
  justify-content: space-between;
  @media only screen and (max-width: 480px) {
    justify-content: center;
    text-align: center;
  }
  .btn {
    margin-top: 10px;
  }
}

.btn {
  @include flc;
  padding: 0.5em 2em 0.4em;
  @include rfs(14);
  white-space: nowrap;
  letter-spacing: 0.4px;
  border-radius: 20px;
  @media only screen and (max-width: 1200px) {
    padding: 0.25em 1em 0.2em;
  }
  @media only screen and (max-width: 480px) {
    white-space: normal;
    padding: 0.25em 0.5em 0.2em;
  }
  .icon {
    margin-left: 0.34em;
    font-size: 1.2em;
  }
}

.btn--transparent {
  background-color: transparent;
  margin-right: 10px;
  &:hover {
    color: #fff;
    .icon {
      fill: #fff;
    }
  }
}

.btn--green {
  margin-left: 3vw;
  color: #fff;
  background-color: #4da568;
  border: solid 1px #51ad6d;
  @media only screen and (max-width: 1600px) {
    margin-left: 0;
  }
  &:hover {
    background-color: darken(#4da568, 5%);
  }
}

// popup stats
.popup-stats {
  position: absolute;
  top: calc( 100% + 20px);
  right: 12px;
  width: 100%;
  display: none;
  z-index: 1000;
  @media only screen and (max-width: 992px) {
    right: 0;
    top: calc( 100% + 5px);
  }
}

.chevronRotate {
  transform: rotate(180deg);
}

.stats-inner {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 26px 15px;
  background-color: #282c3b;
  box-shadow: 0px 5px 5px 0 rgba(0, 0, 0, 0.1);
  &:before {
    content: "";
    position: absolute;
    top: -6px;
    left: 14%;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 6px 6px 6px;
    border-color: transparent transparent #282c3b transparent;
  }
}

// TABLE
.table-wrap {
  // flex-grow: 1;
  margin-top: 20px;
  color: #777a8c;
  background-color: #242734;
}

.table {
  width: 100%;
  font-family: $font-primary;
  thead tr {
    height: 44px;
    font-size: 12px;
    font-weight: 900;
    letter-spacing: 0.3px;
    text-align: center;
    background-color: #242734;
    th {
      vertical-align: middle;
    }
  }
  tbody tr {
    background-color: #1e202a;
    &:not(:last-child) {
      border-bottom: 1px solid #242734;
    }
    td {
      padding-top: 20px;
      padding-bottom: 20px;
      vertical-align: middle;
      text-align: center;
      @media only screen and (max-width: 768px) {
        padding-top: 10px;
        padding-bottom: 10px;
        font-size: 12px;
      }
    }
  }
  .ceil-head__players {
    width: 155px;
    @media only screen and (max-width: 992px) {
      width: auto;
    }
  }
  .ceil-players {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding-left: 15px;
    padding-right: 15px;
    @media only screen and (max-width: 992px) {
      flex-direction: column;
    }
    @media only screen and (max-width: 480px) {
      padding-left: 1%;
      padding-right: 1%;
    }
  }
  .ceil-player {
    @include flc;
    width: 40px;
    height: 40px;
    color: #c9496f;
    border-radius: 4px;
    background-color: transparent;
    border: solid 2px #c9496f;
    img {
      width: 100%;
    }
  }
  .ceil-player2 {
    color: #7e5f9d;
    border: solid 2px #7e5f9d;
  }
  .ceil-unknown {
    border-width: 1px;
  }
  .sign-question {
    font-size: 25px;
    font-weight: 900;
  }
  .versus {
    margin: 5px;
    font-size: 17px;
    font-weight: 900;
    letter-spacing: 1.2px;
  }
  // ceil items
  .ceil-head__items {
    text-align: left;
    @media only screen and (min-width: 1500px) {
      padding-left: 4vw;
    }
  }
  .ceil__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    @media only screen and (min-width: 1500px) {
      padding-left: 4vw;
    }
  }
  .items__weapon {
    @include flc;
    width: 54px;
    height: 45px;
    margin-right: 10px;
    border-radius: 4px;
    background-color: rgba(0, 0, 0, 0.1);
    @media only screen and (max-width: 1366px) {
      margin-bottom: 5px;
    }
    @media only screen and (max-width: 768px) {
      &:nth-child(n+3) {
        display: none;
      }
    }
    @media only screen and (max-width: 380px) {
      width: 36px;
      height: 30px;
    }
    img {
      width: 54px;
      height: 45px;
      object-fit: scale-down;
      @media only screen and (max-width: 380px) {
        width: 36px;
        height: 30px;
      }
    }
  }
  .items__more {
    margin: 10px 0 10px;
    font-family: inherit;
    font-size: 12px;
    letter-spacing: 0.3px;
    color: inherit;
    &:hover {
      cursor: pointer;
      color: #eaeaea;
    }
  }
  // ceil value
  .ceil-head__value {
    width: 300px;
    @media only screen and (max-width: 1600px) {
      width: auto;
    }
  }
  .ceil__value {
    @include flc;
    .icon {
      margin-right: 1px;
      fill: #777a8c;
    }
  }
  // ceil status
  .ceil-head__status {
    padding-left: 20px;
    padding-right: 20px;
    @media only screen and (max-width: 992px) {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
  .status-open {
    font-size: 14px;
    letter-spacing: 0.4px;
    text-align: center;
    @media only screen and (max-width: 768px) {
      font-size: 12px;
    }
  }
  .ceil__chart {
    @include flc;
  }
  // progress start
  $size: 40px;
  @mixin circle($size) {
    content: "";
    position: absolute;
    border-radius: 50%;
    left: calc(50% - #{$size/2});
    top: calc(50% - #{$size/2});
    width: $size;
    height: $size;
  }
  .progress-pie-chart {
    width: $size;
    height: $size;
    border-radius: 50%;
    background-color: #282b38;
    position: relative;
    &.gt-50 {
      background-color: #7e5f9d;
    }
  }
  .ppc-progress {
    @include circle($size);
    clip: rect(0, $size, $size, #{$size/2});
    .ppc-progress-fill {
      @include circle($size);
      clip: rect(0, #{$size/2}, $size, 0);
      background: #7e5f9d;
      transform: rotate(60deg);
    }
    .gt-50 & {
      clip: rect(0, #{$size/2}, $size, 0);
      .ppc-progress-fill {
        clip: rect(0, $size, $size, #{$size/2});
        background: #7e5f9d;
      }
    }
  }
  .ppc-percents {
    @include circle(#{$size/1.1});
    background: #1e202a;
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      font-size: 14px;
      font-weight: 300;
      color: #7e5f9d;
    }
  }
  // end progress
  .pie-chart--rose {
    &.gt-50 {
      background-color: #c9496f;
    }
    .ppc-progress-fill {
      background-color: #c9496f;
    }
    .ppc-percents {
      span {
        color: #c9496f;
      }
    }
  }
  // ceil action
  .ceil__action {
    @include flc;
    flex-wrap: wrap;
    .btn {
      margin: 7px;
    }
  }
  .btn--rose {
    $btn-color: #c9496f;
    color: $btn-color;
    border: 1px solid $btn-color;
    &:hover {
      color: #fff;
      border-color: #fff;
    }
  }
  .btn--purple {
    $btn-color: #7e5f9d;
    color: $btn-color;
    border: 1px solid $btn-color;
    &:hover {
      color: #eaeaea;
      border-color: #eaeaea;
    }
  }
}

// SIDEBAR
.sidebar.is-open {
  right: 0;
}

.sidebar {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 385px;
  padding-right: 7px;
  background-color: #1e202a;
  transition: all 0.4s ease;
  z-index: 100;
  @media only screen and (max-width: 600px) {
    position: absolute;
    right: -320px;
    top: 0;
    width: 320px;
  }
  .sidebar__online {
    padding: 15px 0;
    font-size: 14px;
    text-align: center;
    color: #fff;
    border-bottom: 1px solid #242734;
    @media only screen and (max-height: 680px) {
      padding: 5px 0;
    }
  }
  .chat {
    flex-grow: 1;
    height: 50vh;
    padding: 10px 0 10px 10px;
  }
  .chat__feed {
    height: calc(100% - 50px);
  }
  .chat__item {
    display: flex;
    align-items: flex-start;
    margin-bottom: 6px;
    padding-right: 10px;
    @media only screen and (max-width: 768px) {
      flex-direction: column;
    }
  }
  .chat__photo {
    display: flex;
    margin-right: 15px;
    margin-bottom: 5px;
    background-color: #242734;
    @media only screen and (min-width: 600px) and (max-width: 768px) {
      align-self: flex-end;
      margin-top: 5px;
      margin-right: 0;
      img {
        width: 30px;
        height: 30px;
      }
    }
    img {
      width: 40px;
      height: 40px;
      border-radius: 4px;
      overflow: hidden;
    }
  }
  .chat__message {
    position: relative;
    padding: 9px 10px 6px 10px;
    @include rfs(14);
    background-color: #242734;
    border-radius: 4px;
    &:before {
      content: "";
      position: absolute;
      left: -6px;
      top: 10px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 6px 6px 6px 0;
      border-color: transparent #242734 transparent transparent
    }
  }
  .message__author {
    margin-bottom: 10px;
    font-weight: 900;
    color: white;
  }
  .message__text {
    line-height: 1.2;
  }
  // chat form
  .chat__form-wrap {
    position: relative;
    margin-top: 25px;
    @media only screen and (max-height: 680px) {
      margin-top: 15px;
    }
  }
  .chat__input {
    width: 100%;
    height: 36px;
    padding-right: 63px;
    padding-left: 10px;
    border-radius: 4.1px;
    background-color: #16171f;
    border: none;
    color: #eaeaea;
  }
  .chat__send {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    padding: 5px 13px;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 900;
    color: #777a8c;
    &:hover {
      cursor: pointer;
      color: #eaeaea;
    }
  }
  .sidebar__poster {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    margin: 23px 5px 0 5px;
    img {
      width: 100%;
      border-style: solid;
      border-width: 3px;
      border-image-source: linear-gradient(to top, #ac1104, #fe801a);
      border-image-slice: 1;
    }
    @media only screen and (max-height: 680px) {
      justify-content: flex-end;
      margin-top: 10px;
      img {
        width: auto;
        height: 100px;
      }
    }
    @media only screen and (max-height: 480px) {
      display: none;
    }
  }
  // sidebar footer
  .sidebar__footer {
    margin-top: 10px;
    padding-bottom: 10px;
    padding-right: 7px;
    @media only screen and (min-height: 480px) and (max-height: 640px) {
      margin-top: 15px;
    }
    @media only screen and (max-height: 480px) {
      margin-top: 5px;
    }
  }
  .footer__socials {
    display: flex;
    justify-content: flex-end;
  }
  .socials__item {
    @include flc;
    width: 24px;
    height: 24px;
    margin-left: 10px;
    border-radius: 50%;
    background-color: #777a8c;
    .icon {
      fill: #1e202a;
      font-size: 16px;
    }
  }
  .footer__nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    margin-top: 20px;
    @media only screen and (max-height: 640px) {
      margin-top: 10px;
    }
  }
  .footer__nav-link {
    margin-left: 15px;
    margin-bottom: 5px;
    font-size: 12px;
  }
  .sidebar__footer-copy {
    margin-top: 8px;
    font-size: 12px;
    font-weight: 300;
    text-align: right;
  }
}

// MODALS POPUPS
.modal-popup {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding-bottom: 20px;
  color: #777a8c;
  background-color: #1e202a;
  border-radius: 4px;
  .modal__title {
    padding: 22px 25px 15px 0;
    margin: 0 20px 0 30px;
    font-size: 16px;
    font-weight: 300;
    text-align: left;
    border-bottom: 2px solid #282b38;
    @media only screen and (max-width: 768px) {
      margin: 0 10px 0 10px;
      line-height: 1.2;
    }
  }
  .mfp-close {
    right: 4px;
    top: 7px;
    color: #777a8c;
  }
}

// history popup
#modal-history {
  min-height: 760px;
  thead tr {
    background-color: #1e202a;
  }
  tbody tr td {
    background-color: #242734;
    padding: 15px 0;
  }
  .table .ceil-head__players {
    @media only screen and (min-width: 768px) {
      width: 180px;
    }
  }
  .table .ceil-players {
    @media only screen and (min-width: 768px) {
      padding-left: 35px;
    }
    @media only screen and (min-width: 480px) and (max-width: 992px) {
      flex-direction: row;
      justify-content: flex-start;
    }
  }
  .ceil__value {
    font-size: 14px;
  }
  .ceil-winner {
    display: flex;
    justify-content: center;
  }
  .row__data {
    td {
      padding-top: 0;
      padding-bottom: 10px;
      border-bottom: 1px solid #1e202a;
      text-align: left;
    }
  }
  .ceil__data {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    padding-left: 15px;
    @include rfs(12);
    text-align: left;
    color: #777a8c;
    @media only screen and (min-width: 768px) {
      padding-left: 35px;
    }
    @media only screen and (max-width: 640px) {
      flex-flow: nowrap column;
    }
  }
  .ceil__data-value {
    margin-right: 3%;
    margin-bottom: 5px;
  }
}

// history popup
#modal-set-url {
  min-height: 508px;
  display: flex;
  flex-direction: column;
  .set-url__content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 628px;
    margin: 0 auto;
  }
  .set-url__paragaph {
    margin-top: 20px;
    @include rfs(16);
    line-height: 1.3;
    font-weight: 300;
    a {
      color: #7e5f9d;
    }
  }
  // set-url form
  .set-url__form-wrap {
    position: relative;
    margin-top: 50px;
    text-align: center;
  }
  .set-url__input {
    width: 100%;
    height: 46px;
    padding-right: 30px;
    padding-left: 13px;
    color: #eaeaea;
    @include rfs(16);
    border-radius: 4.1px;
    background-color: #16171f;
    border: none;
    @media only screen and (max-width: 480px) {
      height: 36px;
    }
  }
  input::placeholder {
    font-size: inherit;
    font-weight: 300;
    color: #6e7180;
    font-family: inherit;
  }
  .set-url__tick {
    position: absolute;
    top: 0;
    right: 10px;
    display: flex;
    align-items: center;
    height: 46px;
    padding-right: 5px;
    padding-left: 5px;
    @media only screen and (max-width: 480px) {
      height: 36px;
    }
    .icon {
      font-size: 19px;
      fill: #7e5f9d;
    }
    &:hover {
      cursor: pointer;
      .icon {
        fill: #eaeaea;
      }
    }
  }
  .set-url__submit {
    display: inline-block;
    width: 250px;
    height: 28px;
    margin: 40px auto;
    font-size: 14px;
    letter-spacing: 0.4px;
    text-align: center;
    color: #7e5f9d;
    background-color: #1e202a;
    border: solid 1px #7e5f9d;
    border-radius: 20px;
    &:hover {
      cursor: pointer;
      color: #eaeaea;
      border-color: #eaeaea;
    }
  }
}

// game popup
#modal-view {
  min-height: 640px;
}

.game {
  max-width: 732px;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 40px;
  .game__players {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 20px;
    border-bottom: 1px solid #282b38;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: -1px;
      margin: auto;
      height: 1px;
      width: 20%;
